

















import defineComponent from "wepage-admin/types/defineComponent";
import vHead from "./Header.vue";
import vSidebar from "./Sidebar.vue";
export default defineComponent({
  components: {
    vHead,
    vSidebar
  },
  data() {
    return {
      tagsList: [],
      collapse: false
    };
  },
  created() {
    this.$eventBus.$on("collapse-content", msg => {
      this.collapse = msg;
    });
  }
});
