




























import defineComponent from "wepage-admin/types/defineComponent";
import { mapMutationsTyped, mapStateTyped } from "wepage-admin/types/store";
import { PageLyout, PageComponentOptions } from "wepage-admin/types/page";
import Dialog, { DialogType } from "wepage-admin/components/Dialog";
import { uuid } from "wepage-admin/utils";
type TreeNode = {
  id: string;
  label: string;
  data: PageLyout | PageComponentOptions;
  children: TreeNode[];
};
export default defineComponent({
  components: {
    Dialog
  },
  data() {
    return {
      formData: {
        name: ""
      },
      rules: {
        name: [
          { required: true, message: "请输入图层名称", trigger: "blur" },
          { min: 1, max: 10, message: "长度在 1 到 10 个字符", trigger: "blur" }
        ]
      }
    };
  },
  computed: {
    ...mapStateTyped("page", ["pageConfig", "activeComp", "activeLayout"]),
    treeData(): TreeNode[] {
      console.log("this.pageConfig ====", this.pageConfig);
      const treeData: TreeNode[] = [];

      const LayoutMap = {};
      let temp;
      for (const layout of this.pageConfig.layouts) {
        temp = {
          id: layout.id,
          label: layout.name,
          data: layout,
          children: []
        };
        treeData.push(temp);
        LayoutMap[layout.id] = temp;
      }

      for (const child of this.pageConfig.children) {
        LayoutMap[child.layoutId].children.push({
          id: child.id,
          label: child.alias,
          data: child,
          children: []
        });
      }
      return treeData;
    }
  },
  methods: {
    ...mapMutationsTyped("page", ["removeComponent", "setActiveComp", "addLayout", "removeLayout", "setActiveLayout"]),
    isActive(node, data) {
      if (node.level === 1) {
        return this.activeLayout && data.id == this.activeLayout.id;
      } else {
        return this.activeComp && data.id == this.activeComp.id;
      }
    },
    isDeactive(node, data) {
      if (node.level === 1) {
        return !this.activeLayout || data.id !== this.activeLayout.id;
      } else {
        return !this.activeComp || data.id !== this.activeComp.id;
      }
    },
    remove(data: TreeNode, node) {
      if (node.level === 1) {
        this.removeLayout(data.id);
      } else {
        this.removeComponent(data.id);
      }
    },
    active(data: TreeNode, node) {
      if (node.level === 1) {
        this.setActiveLayout(data.id);
      } else {
        this.setActiveComp(data.id);
      }
    },
    deactive(node) {
      if (node.level === 1) {
        this.setActiveLayout(null);
      } else {
        this.setActiveComp(null);
      }
    },
    showAddDialog() {
      ((this.$refs.dialog as any) as DialogType).show({
        title: "新增图层",
        renderContent: () => (
          <div>
            <el-form ref="form" {...{ props: { model: this.formData } }} rules={this.rules} label-width="80px">
              <el-form-item label="名称" prop="name">
                <el-input vModel={this.formData.name}></el-input>
              </el-form-item>
            </el-form>
          </div>
        ),
        confirm: close => {
          (this.$refs.form as any).validate(valid => {
            if (valid) {
              this.addLayout({
                id: uuid(),
                name: this.formData.name,
                zIndex: 0,
                show: true,
                mode: "position"
              });
              close();
            }
          });
        }
      });
    }
  }
});
