import Vue from "vue";
import { ComponentOptions, PropValidator } from "vue/types/options";
import { ExtendProps } from "./extendProps";

type DataDef<Data, Props, V> = Data | ((this: Readonly<Props> & V) => Data);
export type RecordPropsDefinition<T> = {
  [K in keyof T]: PropValidator<T[K]>;
};
type CombinedVueInstance<Instance extends Vue, Data, Methods, Computed, Props, ExtendProps> = Data & Methods & Computed & Props & Instance & ExtendProps;
type ThisTypedWithInterface<Data, Methods, Computed, Props, V extends Vue = Vue> = object &
  ComponentOptions<V, DataDef<Data, Props, V>, Methods, Computed, RecordPropsDefinition<Props>, Props> &
  ThisType<CombinedVueInstance<V, Data, Methods, Computed, Readonly<Props>, ExtendProps>>;

export default function defineComponent<Data, Methods, Computed, Props, V extends Vue = Vue>(options?: ThisTypedWithInterface<Data, Methods, Computed, Props, V>) {
  return Vue.extend<Data, Methods, Computed, Props>(options as any);
}
